.App {
  background-color: #e7ece8;
}

.btn-custom{
  color: #e7ece8;
  background-color: #e9735c;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}
h1, h2{
  text-align: center;
  color: #541e19;
}
.header {
  height: 8em;
  background-color: #ae392c;
}
.header a {
  color: #e7ece8;
}
.oi-laptop{
  color: #e9735c;
  width: 20px;
  height: 20px;
}
.oi-menu{
  color: #e7ece8;
}
.footer{
  text-align: center;
  height: 5em;
  background-color: #541e19;
}
.footer p {
  margin: 0;
  color: #f27c66;
}
.leaflet-container {
    height: 500px;
    width: 100%;
    border: thick double #541e19;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}



